<script>
export default {
  showLoader: () => {
    $('.loader').show();
  },
  closeLoader: () => {
    $('.loader').addClass('loader-close');
  },
  mounted() {
    $('.loader>.d').first().on('animationend', (event) => {
      if (event.originalEvent.animationName.startsWith('left')) {
        $('.loader').hide().removeClass('loader-close');
      }
    })
  }
}
</script>

<style scoped>

* {
  padding: 0;
  margin: 0;
  -ms-overflow-style: none;
  scrollbar-width: none
}

::-webkit-scrollbar {
  display: none
}

.loader {
  margin: 0;
  padding: 0;
  position: fixed;
  height: 100vh;
  width: 100%;
  z-index: 9999999;
}

.d {
  margin: 0;
  padding: 0;
  position: absolute;
  height: 100vh;
  width: 50%;
  top: 0;
  overflow: hidden
}

.loader svg {
  width: 100%;
  height: auto;
  --time: 2000s
}

.loader-close svg path {
