  stroke-dasharray: var(--k);
  animation: char var(--time) linear infinite;
  transition: 1s linear
}

.loader svg path:nth-child(12) {
  --d: 262px;
  stroke-dasharray: var(--d);
  animation: char var(--time) linear infinite;
  transition: 1s linear
}

