  --m: 333px;
  stroke-dasharray: var(--m);
  animation: char var(--time) linear infinite;
  transition: 1s linear
}

.loader svg path:nth-child(20) {
  --r: 356px;
  stroke-dasharray: var(--r);
  animation: char var(--time) linear infinite;
  transition: 1s linear
}

.loader-close svg path {
  stroke-width: 0
}

@keyframes char {
  100% {
    stroke-dashoffset: 100000
  }
}

.loader .left {
  left: 0;
  background: #000
}

.loader-close .left {
  animation: left 3s forwards
}

.loader .left svg {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(50%, -50%)
}

.loader .right {
  right: 0;
