import VueLogger from 'vuejs-logger';
import Vue from "vue";

const loggerOptions = {
    isEnabled: true,
    logLevel: __PRODUCTION__ ? 'error' : 'debug',
    stringifyArguments: true,
    showLogLevel: true,
    showMethodName: true,
    separator: '|',
    showConsoleColors: true
};

Vue.use(VueLogger, loggerOptions);

Vue.$log.debug('Включен режим разработчика');