import Vue from "vue";

import RSocketWebSocketClient from "rsocket-websocket-client";
import {BufferEncoders, MESSAGE_RSOCKET_COMPOSITE_METADATA, RSocketClient, toBuffer} from "rsocket-core";
import Metadata from "@/rsocket/metadata";
import Sleep from "@/utils/sleep";
import notification from "@/notify/notification";

export default class Client {
    constructor(wsUrl, keepAlive = 60000, lifetime = 180000, dataMimeType = "application/json") {
        this.client = new RSocketClient({
            "setup": {
                "keepAlive": keepAlive,
                "lifetime": lifetime,
                "dataMimeType": dataMimeType,
                "metadataMimeType": MESSAGE_RSOCKET_COMPOSITE_METADATA.string
            },
            "transport": new RSocketWebSocketClient({
                "url": wsUrl
            }, BufferEncoders)
        });
    }

    bearerAuth(token) {
        this.auth = {type: "bearer", token: token}
    }

    simpleAuth(username, password) {
        this.auth = {type: "simple", username: username, password: password}
    }

    nullAuth() {
        this.auth = null;
    }

    async connect(
        completeCallback = (socket) => {
        }, errorCallback = (error) => {
        }, subscribeCallback = (cancel) => {
        }
    ) {
        await this.client.connect().subscribe({
            onComplete: socket => {
                this.connection = socket;
                completeCallback(socket);
            },
            onError: error => {
                this.connection = [];
                errorCallback(error);
            },
            onSubscribe: cancel => {
                subscribeCallback(cancel);
            }
        });
        await Sleep.await(async () => {
            while (!this.connection) {
                await Sleep.delay(10);
            }
        })
    }

    async requestResponse(data, route,
                          completeCallback = (data) => {
                          },
                          errorCallback = (error) => {
                          },
                          subscribeCallback = (cancel) => {
                          }
    ) {
        if (this.connection == null) {
            Vue.$log.error("Вы не можете отправлять запросы, пока отсутствует соединение");
            notification("error", "Соеднинение с сервером отсутствует, попробуйте позже");
        }
        const metadata = new Metadata({
            route: route,
            auth: this.auth
        }).toMetadata();
        data = toBuffer(data);
        let isEnd = false;
        let res = [];
        this.connection.requestResponse({
            data,
            metadata
        }).subscribe({
            onComplete: (data) => {
                completeCallback(data);
                res = data;
                isEnd = true;
            },
            onError: (error) => {
                error.prettyText = error.toString().matchAll(/\): (.*)\. See error `source`/gm).next().value[1];
                errorCallback(error);
                isEnd = true;
            },
            onSubscribe: (cancel) => {
                subscribeCallback(cancel);
            }
        });
        await Sleep.until(async () => {
            while (!isEnd) {
                await Sleep.delay(10);
            }
        }, 120000);
        return res;
    }
}