import Connection from "@/rsocket/connection";
const dashboardRoutes = [
    {
        path: "/dashboard/change-password",
        component: () => import("@/views/dashboard/pages/ChangePassword")
    },
    {
        path: "/dashboard/admin/users",
        component: () => import("@/views/dashboard/pages/admin/UserList")
    },
    {
        path: "/dashboard/talkytime/user-roles/:id",
        component: () => import("@/views/dashboard/pages/admin/EditUserRoles")
    },
    {
        path: "/dashboard/admin/create-user",
        component: () => import("@/views/dashboard/pages/admin/CreateUser")
    },
    {
        path: "/dashboard/talkytime/create",
        component: () => import("@/views/dashboard/pages/talkytime/CreateTalkyTime")
    },
    {
        path: "/dashboard/talkytime/accounts",
        component: () => import("@/views/dashboard/pages/talkytime/TalkyTimeList")
    },
    {
        path: "/dashboard/talkytime/edit/:id",
        component: () => import("@/views/dashboard/pages/talkytime/EditTalkyTime")
    },
    {
        path: "/dashboard/talkytime/ladies",
        component: () => import("@/views/dashboard/pages/talkytime/TalkyTimeLadyList")
    },
    {
        path: "/dashboard/talkytime/lady/:id",
        component: () => import("@/views/dashboard/pages/talkytime/TalkyTimeLady")
    },
    {
        path: "/dashboard/talkytime/finances",
        component: () => import("@/views/dashboard/pages/talkytime/TalkyTimeFinanceList")
    },
    {
        path: "/logout",
        component: () => {
            Connection.signOut();
        }
    }
]

export default dashboardRoutes