import $ from 'jquery';

class cSleep {
    delay(ms) {
        return new Promise(resolve => setTimeout(resolve, ms));
    }
    await(func) {
        return Promise.any([func()]);
    }
    until(func, maxSleepTimeInMs) {
        return Promise.race([this.delay(maxSleepTimeInMs), func()]);
    }
    async awaitSelection(selection, maxLatency) {
        return await this.until(async () => {
            while (!$(selection)[0]) {
                await this.delay(10);
            }
            return $(selection);
        }, maxLatency) || $(selection);
    }
}

const Sleep = new cSleep();

export default Sleep;
