import '@/constant/imports';

import Loader from "@/views/loader/Loader";
import App from '@/App';
import Vue from 'vue'

import Connection from "@/rsocket/connection";
import router from "@/views/router/routes";

new Vue({
    render: (h) => h(Loader)
}).$mount("#loader");

(async function () {
    Loader.showLoader();
    await Connection.connect();

    new Vue({
        router,
        render: (h) => h(App)
    }).$mount('#app');
    Loader.closeLoader();
})();
